.main-nav {
  margin: 0 auto;
  text-align: center;
  position: relative;
  font-weight: 700;

  @media (max-width: @mobile-width-only) {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    font-size: 18px;
    line-height: 26px;
    background-color: #81b3d2;
    color: #ffffff;
    z-index: 2;
  }

  @media (min-width: @tablet-width) {
    background-color: #ffffff;
    display: flex;
    justify-content: space-between;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav__wrapper {
  @media (min-width: @tablet-width) {
    width: 306px;
  }

  @media (min-width: @desktop-width) {
    width: 478px;
  }
}

.main-nav__list {
  .clear-list;

  @media (min-width: @tablet-width) {
    display: flex;
  }
}

.main-nav__item {
  padding: 17px 0 12px 0;

  @media (max-width: @mobile-width-only) {
    border-top: 1px solid #01010133;
    border-bottom: 1px solid #ffffff33;
  }

  @media (min-width: @tablet-width) {
    width: 153px;
  }

  @media (min-width: @desktop-width) {
    width: 239px;
  }

  &--main {
    border-top: none;
  }

  &--html-academy {
    border-bottom: none;
  }
}

.main-nav--closed .main-nav__item {
  @media (max-width: @mobile-width-only) {
    display: none;
  }
}

.main-nav__link {
  color: #ffffff;

  @media (min-width: @tablet-width) {
    color: #000000;
  }

  &--active {
    @media (min-width: @tablet-width) {
      color: #766357;
    }
  }

  &:hover {
    @media (min-width: @tablet-width) {
      color: #81b3d2;
    }
  }

  // &:focus {

  // }

  &:active {
    color: #ffffff4d;

    @media (min-width: @tablet-width) {
      color: #000000;
      opacity: 30%;
    }
  }
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  padding: 0;
  width: 23px;
  height: 22px;
  border: none;
  cursor: pointer;
  outline: none;
  background-color: transparent;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  background-image: url("../../img/signs/icon-cross.svg");
  background-repeat: no-repeat;
  background-position: center;

  &:hover {
    opacity: 60%;
  }

  &:active {
    opacity: 30%;
  }
}

.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: #81b3d2;
  height: 4px;
  width: 23px;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;

  &:hover {
    // box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }

  &:active {
    // box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }
}

.main-nav--nojs {
  position: relative;
  z-index: 1;
}

.main-nav--nojs .main-nav__item {
  display: block;
}
